.am-image-picker-list .am-image-picker-item .am-image-picker-item-content {
  height: 100%;
  width: 100%;
  border-radius: 3px;
  background-size: cover;
}
.am-image-picker-list .am-image-picker-item img {
  width: 100%;
}
.am-image-picker-list .am-image-picker-upload-btn {
  box-sizing: border-box;
  border-radius: 3px;
  border: 1PX solid #ddd;
  background-color: #fff;
}
.am-image-picker-list .am-image-picker-upload-btn:before,
.am-image-picker-list .am-image-picker-upload-btn:after {
  width: 1PX;
  height: 25px;
  content: " ";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #ccc;
}
.am-image-picker-list .am-image-picker-upload-btn:after {
  width: 25px;
  height: 1PX;
}
.am-image-picker-list .am-image-picker-upload-btn-active {
  background-color: #ddd;
}
.am-image-picker-list .am-image-picker-upload-btn input {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
}

.am-icon {
  fill: currentColor;
  background-size: cover;
  width: 22px;
  height: 22px;
}
.am-icon-xxs {
  width: 15px;
  height: 15px;
}
.am-icon-xs {
  width: 18px;
  height: 18px;
}
.am-icon-sm {
  width: 21px;
  height: 21px;
}
.am-icon-md {
  width: 22px;
  height: 22px;
}
.am-icon-lg {
  width: 36px;
  height: 36px;
}
.am-icon-loading {
  -webkit-animation: cirle-anim 1s linear infinite;
          animation: cirle-anim 1s linear infinite;
}
@-webkit-keyframes cirle-anim {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes cirle-anim {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.am-toast {
  position: fixed;
  width: 100%;
  z-index: 1999;
  font-size: 14px;
  text-align: center;
}
.am-toast > span {
  max-width: 50%;
}
.am-toast.am-toast-mask {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
  top: 0;
  -webkit-transform: translateZ(1px);
          transform: translateZ(1px);
}
.am-toast.am-toast-nomask {
  position: fixed;
  max-width: 50%;
  width: auto;
  left: 50%;
  top: 50%;
  -webkit-transform: translateZ(1px);
          transform: translateZ(1px);
}
.am-toast.am-toast-nomask .am-toast-notice {
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}
.am-toast-notice-content .am-toast-text {
  min-width: 60px;
  border-radius: 3px;
  color: #fff;
  background-color: rgba(58, 58, 58, 0.9);
  line-height: 1.5;
  padding: 9px 15px;
}
.am-toast-notice-content .am-toast-text.am-toast-text-icon {
  border-radius: 5px;
  padding: 15px 15px;
}
.am-toast-notice-content .am-toast-text.am-toast-text-icon .am-toast-text-info {
  margin-top: 6px;
}

.am-navbar {
  display: flex;
  align-items: center;
  height: 45px;
  background-color: #108ee9;
  color: #fff;
}
.am-navbar-left,
.am-navbar-title,
.am-navbar-right {
  flex: 1 1;
  height: 100%;
  display: flex;
  align-items: center;
}
.am-navbar-left {
  padding-left: 15px;
  font-size: 16px;
}
.am-navbar-left-icon {
  margin-right: 5px;
  display: inherit;
}
.am-navbar-title {
  justify-content: center;
  font-size: 18px;
  white-space: nowrap;
}
.am-navbar-right {
  justify-content: flex-end;
  font-size: 16px;
  margin-right: 15px;
}
.am-navbar-light {
  background-color: #fff;
  color: #108ee9;
}
.am-navbar-light .am-navbar-title {
  color: #000;
}

